import Vue from 'vue';
import _elementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import reTable from './reTable';
Vue.use(_elementUI);

const coms = [reTable];
/* 
 Vue.js  install 
 install install  Vue 

 new Vue() 

 install 
*/


// install Vueuse
const install = function () {
  // 
  if (install.installed) {
    return;
  }
  coms.forEach(com => {
    console.log('com', com);
    //  ( Vue.extend)
    Vue.component(com.name, com);
  });
};
// 
if (typeof window !== 'undefined' && window.Vue && window._elementUI) {
  install(window.Vue);
  install(window._elementUI);
}
// const VueQuillEditor = { Quill, quillEditor, install }

const VueEleUI = { install };
export default VueEleUI; // use